html,
body {
  padding:0;
  margin:0;

// animation: body-ani 15s infinite ease-in-out;

}

p,
a,
h1,
h2 {
  padding:0;
  margin:0;
}
@keyframes body-ani {
  0% {background-image: url("https://wallpapercave.com/wp/wp2869225.jpg");}
  50% {background-image: url("https://static.giantbomb.com/uploads/original/1/10333/2702699-mge%20screenshot%202014-11-20%2013.56.22.869.png");

}
100% {background-image: url("http://wallfon.com/walls/games/morrowind-morning.jpg");}
}

@keyframes shadow {
  from {text-shadow: 0 0 3px black;}
  50% {text-shadow: 0 0 30px black;}
  to {text-shadow: 0 0 3px black;}
}
* {
  outline:2px solid green;
  outline-offset: -2px;
}
