}

.wd-80 {
  width: 80%;
}

body,
div {
  margin:0;
  padding:0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
}
body {
  // height: 100vh;
}
div {
  height: 100vh;
  padding: 5%;
// visibility: visible;

}

p {
  font-size: 1em;
}
.start {
  padding: 2%;
  font-size: 200%;
  border-radius: 15px;
  background-color: transparent;
  text-shadow: 1px 5px 5px black;
}



.hidden {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  clip: rect(0 0 0 0);

}
.showing {
  clip: auto;
  visibility: visible;
}